body { 
  margin:20px; /* The image used */
  /* background-image: url(./images/bg-image.png); */
  background-color: #d8dde9;
  /* Full height */
  height: 100%; 

  /* Center and scale the image nicely */
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.AuctionBody { 
  margin:10px; /* The image used */
  background-image: url(./images/cricket_stadium.png);
  /* background-color: #d8dde9; */
  /* Full height */
  height: 100%; 
  min-height: 85vh;
  /* Center and scale the image nicely */
  background-position: center;
  background-repeat: no-repeat;
  background-size: auto;
}

.playerName {
  text-align: center; 
  min-height: 8vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(50px + 2vmin);
  color: white;
}

.playerInfo {
  text-align: center; 
  min-height: 6vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(25px + 2vmin);
  color: yellow;
}

.table {
  text-align: center; 
  min-height: 4vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(20px + 2vmin);
  color: white;
  border: #C0C0C0;
  border-block-width: 2px;
}

.td {

  min-height: 4vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(20px + 2vmin);
  color: white;
  width: "30%";
  border: #C0C0C0;
}

.holdEmptySpace{
  display: block;
  min-height: 25vh;
}

.mtplPlayerInfo{
  text-align: center; 
  min-height: 6vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(25px + 2vmin);
  color: yellow;
}

.holdMTPLSpace{
  display: block;
  min-height: 8vh;
}

.mtplInfo {
  text-align: center; 
  min-height: 8vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(30px + 2vmin);
  color: yellow;
}

.basePrice {
  text-align: center; 
  min-height: 8vh;
  display: flex;
  flex-direction: column;
  align-items: right;
  justify-content: center;
  font-size: calc(30px + 2vmin);
  color: black;
  background-color: #e9fa03;
}

.externalPlayer {
  text-align: center; 
  min-height: 4vh;
  display: flex;
  flex-direction: column;
  align-items: right;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #C0C0C0;
  background-color: #B22222;
}

.App {
  text-align: center; 
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 4vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
